import React, {Component} from 'react'
import cx from 'classnames'
import Card from './Components/Card'
import './App.css'

class App extends Component {
  state = {
    data: [
      {
        title: '1',
        desc: '',
        id: 1213,
      },
      {
        title: ' / 2',
        desc: '',
        id: 2213,
      },
      {
        title: '3',
        desc: '',
        id: 3123,
      },
    ],
    startAni: false,
  }

  handleSlideSuccess = () => {
    this.setState({
      data: this.state.data.slice(1),
    })
  }

  handleStartAni = status => {
    this.setState({
      startAni: status,
    })
  }

  render() {
    const {data, startAni} = this.state
    return (
      <div className="App">
        {data
          .slice(0, 2)
          .reverse()
          .map((value, index) => (
            <Card
              content={value}
              key={value.id}
              className={cx({
                'Card-bottom': index === 0,
                'Card-top': startAni,
              })}
              onSlideSuccess={this.handleSlideSuccess}
              onStartAni={this.handleStartAni}
            />
          ))}
      </div>
    )
  }
}

export default App



